import React, { Component } from 'react';
import ReactAudioPlayer from 'react-audio-player';
import { get, isNull } from 'lodash'

import {
  likeServiceRecord,
  dislikeServiceRecord,
  listenServiceRecord,
  recordServiceRecord,
  downloadServiceRecord,
} from "../shared/services/requestService"

import { withRecord } from "../shared/containers/containerRecord"

import styles from './record.module.scss'

const localStorageName = "twoGeeseRecords"
const localStorageDefault = {
  like: false,
  dislike: false,
  listen: false,
  download: false,
}

class Record extends Component {
  constructor(props) {
    super(props);

    this.state = {
      likes: 0,
      dislikes: 0,
      listens: 0,
      localStorageData: {},
    }
  }

  handleLike() {
    const { record } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[record.id].like && !localStorageData[record.id].dislike) {
      likeServiceRecord.request({ id: record.id })
        .then((result) => {
          localStorageData[record.id].like = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))

          const data = result.data
          this.setState({
            likes: data[record.id].like
          })
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }

  handleDislike() {
    const { record } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[record.id].dislike && !localStorageData[record.id].like) {
      dislikeServiceRecord.request({ id: record.id })
        .then((result) => {
          localStorageData[record.id].dislike = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))

          const data = result.data
          this.setState({
            dislikes: data[record.id].dislike
          })
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }

  handleListen() {
    const { record } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[record.id].listen) {
      listenServiceRecord.request({ id: record.id })
        .then((result) => {
          localStorageData[record.id].listen = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))

          const data = result.data
          this.setState({
            listens: data[record.id].listen
          })
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
  handleDownload() {
    const { record } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[record.id].download) {
      downloadServiceRecord.request({ id: record.id })
        .then((result) => {
          localStorageData[record.id].download = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }

  componentDidMount() {
    const { record } = this.props

    recordServiceRecord.request({ id: record.id })
      .then((result) => {
        const data = result.data

        let localStorageData = !!localStorage.getItem(localStorageName)
          ? JSON.parse(localStorage.getItem(localStorageName))
          : {}

        if (isNull(get(localStorageData, [record.id], null))) {
          localStorageData[record.id] = { ...localStorageDefault }
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))
        }

        this.setState({
          likes: data.like,
          dislikes: data.dislike,
          listens: data.listen,
          localStorageData: localStorageData,
        })
      })
      .catch((error) => {
        console.log(error)
      })
  }

  render() {
    const { onCloseRecord, record } = this.props
    const { likes, dislikes, listens, localStorageData } = this.state

    return <div className={styles.recordMobileBlock}>
      <button className={styles.closeButton} onClick={onCloseRecord} />

      <div className={styles.recordContent}>
        <div className={styles.recordWrapper}>
          <ReactAudioPlayer
            src={record.url}
            onPlay={this.handleListen.bind(this)}
            controls
            style={{ width: '100%' }}
          />
        </div>
        <div className={styles.recordControls}>
          <span className={styles.recordViews}>Play: {listens}</span>
          <a href={record.dUrl} download className={styles.download}>Download</a>
          <button
            className={get(localStorageData, [record.id, "like"], false) ? styles.likeButtonSelected : styles.likeButton}
            onClick={this.handleLike.bind(this)}
            disabled={get(localStorageData, [record.id, "dislike"], false) || get(localStorageData, [record.id, "like"], false)}
          >
            {likes}
            <img src="/img/like-s-icon.png" alt="like-icon" />
          </button>
          <button
            className={get(localStorageData, [record.id, "dislike"], false) ? styles.disLikeButtonSelected : styles.disLikeButton}
            onClick={this.handleDislike.bind(this)}
            disabled={get(localStorageData, [record.id, "dislike"], false) || get(localStorageData, [record.id, "like"], false)}
          >
            <img src="/img/dislike-s-icon.png" alt="like-icon" />
            {dislikes}
          </button>
        </div>
      </div>

      <h1 className={styles.title}>{record.name}</h1>
      <span className={styles.address}>Records / {record.name}</span>
    </div>
  }
}

export default withRecord(Record);