import React, { useState } from 'react';

import { withVideos } from '../shared/containers/containerVideos'
import { withPlayer } from './../shared/containers/containerPlayer'
import { withFiles } from '../shared/containers/containerFiles'
import { withDoc } from './../shared/containers/containerDoc'
import { withRecords } from '../shared/containers/containerRecords'
import { withRecord } from '../shared/containers/containerRecord'
import { withCart } from './../shared/containers/containerCart'
import { withPusk } from './../shared/containers/containerPusk'
import { withIcq } from './../shared/containers/containerIcq'

import Player from './Player'
import Videos from './Videos'
import Doc from './Doc'
import Files from './Files'
import Records from './Records'
import Record from './Record'
import Cart from './Cart'
import Pusk from './Pusk'
import Icq from './Icq'

import styles from './app.module.scss'

const App = (props) => {
  const date = new Date()
  const h = date.getHours() < 10 ? `0${date.getHours()}` : date.getHours()
  const m = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes()
  const [hours, setHour] = useState(h);
  const [minutes, setMinute] = useState(m);
  const { isOpenVideos, onCloseVideos, onOpenVideos,
    isOpenCart, onOpenCart, onCloseCart,
    isOpenPlayer,
    onOpenPusk, onClosePusk, isOpenPusk,
    isOpenIcq, onOpenIcq, onCloseIcq,
    isOpenRecords, onCloseRecords, onOpenRecords,
    isOpenRecord,
    isOpenDoc, 
    isOpenFiles, onOpenFiles, onCloseFiles } = props

  return (
    <div className={styles.wrapper}>
      <div className={styles.back}>
        <div className={styles.itemsWorkSpace}>
          <div className={styles.itemSpace}>
            <button
              className={isOpenVideos ? styles.buttonSpaceActive : styles.buttonSpace}
              onClick={!isOpenVideos ? onOpenVideos : onCloseVideos}
            >
              <img src="/img/comp-icon.png" alt="comp-icon" />
              <span>Disk X</span>
            </button>
          </div>
          <div className={styles.itemSpace}>
            <button
              className={isOpenRecords ? styles.buttonSpaceActive : styles.buttonSpace}
              onClick={!isOpenRecords ? onOpenRecords : onCloseRecords}
            >
              <img src="/img/audios-icon.png" alt="audios-icon" />
              <span>Disk Y</span>
            </button>
          </div>
          <div className={styles.itemSpace}>
            <button
              className={isOpenFiles ? styles.buttonSpaceActive : styles.buttonSpace}
              onClick={!isOpenFiles ? onOpenFiles : onCloseFiles}
            >
              <img src="/img/disk-icon.png" alt="Disk-icon" />
              <span>Disk Z</span>
            </button>
          </div>
          <div className={styles.itemSpace}>
            <button
              className={isOpenIcq ? styles.buttonSpaceActive : styles.buttonSpace}
              onClick={!isOpenIcq ? onOpenIcq : onCloseIcq}
            >
              <img src="/img/icq-icon.png" alt="icq-icon" />
              <span>ICQ</span>
            </button>
          </div>
          <div className={styles.itemSpace}>
            <a target="_blank" href="https://t.me/ev0Ixyz" className={styles.buttonSpace}>
              <img src="/img/telegram-white-icon.png" alt="telegram-icon" />
              <span>Telegram</span>
            </a>
          </div>
          <div className={styles.itemSpace}>
            <a target="_blank" href="https://instagram.com/eevg28/" className={styles.buttonSpace}>
              <img src="/img/instagram-icon.png" alt="telegram-icon" />
              <span>Instagram</span>
            </a>
          </div>
          <div className={styles.itemSpace}>
            <a target="_blank" href="http://twitter.com/eevg28" className={styles.buttonSpace}>
              <img src="/img/twitter-icon.png" alt="telegram-icon" />
              <span>Twitter</span>
            </a>
          </div>
          <div className={styles.itemSpace}>
            <button className={styles.buttonSpace}>
              <img src="/img/xyz-icon.jpg" alt="XYZ-icon" />
              <span>XYZ</span>
            </button>
          </div>
          <div className={styles.itemSpace}>
            <a target="_blank" href="https://t.me/joinchat/OlFHgMG-i-RiMjli" className={styles.buttonSpace}>
              <img src="/img/yo-icon.png" alt="Yo!-icon" />
              <span>Yo!net</span>
            </a>
          </div>
        </div>
        <div className={styles.itemSpaceCart}>
          <button
            className={isOpenCart ? styles.buttonSpaceActive : styles.buttonSpace}
            onClick={!isOpenCart ? onOpenCart : onCloseCart}
          >
            <img src="/img/cart-icon.png" alt="cart-icon" />
            <span>Cart</span>
          </button>
        </div>

        {
          isOpenPlayer
            ? <Player />
            : null
        }
        {
          isOpenVideos
            ? <Videos />
            : null
        }
        {
          isOpenDoc
            ? <Doc />
            : null
        }
        {
          isOpenFiles
            ? <Files />
            : null
        }
        {
          isOpenRecords
            ? <Records />
            : null
        }
        {
          isOpenRecord
            ? <Record />
            : null
        }
        {
          isOpenCart
            ? <Cart />
            : null
        }
        {
          isOpenIcq
            ? <Icq />
            : null
        }
        {
          isOpenPusk
            ? <React.Fragment>
              <Pusk />
              <div className={styles.puskWhiteSpace} onClick={onClosePusk} />
            </React.Fragment>
            : null
        }
      </div>


      <div className={styles.pusk}>
        <div className={styles.puskMenuWrapper}>
          <button className={!isOpenPusk ? styles.puskButton : styles.puskButtonActive} onClick={isOpenPusk ? onClosePusk : onOpenPusk}></button>
        </div>
        <div className={styles.puskTimeWrapper}>
          <span className={styles.puskTime}>{hours}:{minutes}</span>
        </div>
      </div>
    </div>
  )
}

export default withVideos(withCart(withPlayer(withPusk(withIcq(withRecords(withRecord(withFiles(withDoc(App)))))))));