import React, { Component } from 'react';
import ReactPlayer from 'react-player'
import { get, has, isNull } from 'lodash'

import { addResizeListenerIsMobile, isMobile } from "./../shared/services/responsive.service"
import {
  likeServiceVideo,
  dislikeServiceVideo,
  viewServiceVideo,
  videoServiceVideo,
  downloadServiceVideo,
  saveProgressServiceVideo,
} from "../shared/services/requestService"

import { withVideos } from "../shared/containers/containerVideos"
import { withPlayer } from "../shared/containers/containerPlayer"

import styles from './player.module.scss'

const localStorageName = "twoGeeseVideos"
const localStorageDefault = {
  progress: 0,
  like: false,
  dislike: false,
  view: false,
  download: false,
}

class Player extends Component {
  constructor(props) {
    super(props);

    this.state = {
      likes: 0,
      dislikes: 0,
      views: 0,
      localStorageData: {},
      isMobile: isMobile()
    }
  }

  handleLike() {
    const { player } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[player.id].like && !localStorageData[player.id].dislike) {
      likeServiceVideo.request({ id: player.id })
        .then((result) => {
          localStorageData[player.id].like = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))

          const data = result.data
          this.setState({
            likes: data[player.id].like
          })
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }

  handleDislike() {
    const { player } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[player.id].dislike && !localStorageData[player.id].like) {
      dislikeServiceVideo.request({ id: player.id })
        .then((result) => {
          localStorageData[player.id].dislike = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))

          const data = result.data
          this.setState({
            dislikes: data[player.id].dislike
          })
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }

  handleView() {
    const { player } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[player.id].view) {
      viewServiceVideo.request({ id: player.id })
        .then((result) => {
          localStorageData[player.id].view = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))

          const data = result.data
          this.setState({
            views: data[player.id].view
          })
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
  handleDownload() {
    const { player } = this.props
    const { localStorageData } = this.state

    if (!localStorageData[player.id].download) {
      downloadServiceVideo.request({ id: player.id })
        .then((result) => {
          localStorageData[player.id].download = true
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }

  componentWillUnmount() {
    const { player } = this.props
    const { localStorageData } = this.state

    localStorageData[player.id].progress = this.player.getCurrentTime()
    localStorage.setItem(localStorageName, JSON.stringify(localStorageData))
  }

  componentDidMount() {
    const { player } = this.props

    addResizeListenerIsMobile(this, "isMobile")

    videoServiceVideo.request({ id: player.id })
      .then((result) => {
        const data = result.data

        let localStorageData = !!localStorage.getItem(localStorageName)
          ? JSON.parse(localStorage.getItem(localStorageName))
          : {}

        if (isNull(get(localStorageData, [player.id], null))) {
          localStorageData[player.id] = { ...localStorageDefault }
          localStorage.setItem(localStorageName, JSON.stringify(localStorageData))
        }

        this.player.seekTo(localStorageData[player.id].progress, "seconds")

        this.setState({
          likes: data.like,
          dislikes: data.dislike,
          views: data.view,
          localStorageData: localStorageData,
        })
      })
      .catch((error) => {
        console.log(error)
      })
  }

  ref = player => {
    this.player = player
  }

  render() {
    const { onClosePlayer, openVideo, player } = this.props
    const { views, likes, dislikes, localStorageData, isMobile } = this.state

    return <div className={!isMobile ? styles.playerBlock : styles.playerMobileBlock}>
      <button className={styles.closeButton} onClick={onClosePlayer} />

      <div className={styles.playerContent}>
        <div className={styles.playerWrapper}>
          {
            !isMobile
              ?
              <ReactPlayer
                ref={this.ref}
                url={player.url}
                controls={true}
                volume={0.5}
                width={550}
                height={310}
                onPlay={this.handleView.bind(this)}
              />
              :
              <ReactPlayer
                ref={this.ref}
                url={player.url}
                controls={true}
                volume={0.8}
                width={315}
                height={250}
                onPlay={this.handleView.bind(this)}
              />
          }
        </div>
        <div className={styles.playerControls}>
          <span className={styles.playerViews}>Views: {views}</span>
          <a href={`${player.dUrl}`} download className={styles.download} onClick={this.handleDownload.bind(this)}>Download</a>
          <button
            className={get(localStorageData, [player.id, "like"], false) ? styles.likeButtonSelected : styles.likeButton}
            onClick={this.handleLike.bind(this)}
            disabled={get(localStorageData, [player.id, "dislike"], false) || get(localStorageData, [player.id, "like"], false)}
          >
            {likes}
            <img src="/img/like-s-icon.png" alt="like-icon" />
          </button>
          <button
            className={get(localStorageData, [player.id, "dislike"], false) ? styles.disLikeButtonSelected : styles.disLikeButton}
            onClick={this.handleDislike.bind(this)}
            disabled={get(localStorageData, [player.id, "dislike"], false) || get(localStorageData, [player.id, "like"], false)}
          >
            <img src="/img/dislike-s-icon.png" alt="like-icon" />
            {dislikes}
          </button>
        </div>
      </div>

      <h1 className={styles.title}>{player.name}</h1>
      <span className={styles.address}>{has(openVideo, ["name"]) ? `${openVideo.name} / ` : ""}{player.name}</span>
    </div>
  }
}

export default withPlayer(withVideos(Player));